import React, { useCallback, useState } from "react";
import "./App.css";
import { Button, Col, Input, Row, Space } from "antd";
import HanziWriter from "hanzi-writer";
import Grid from "./components/Grid";

const svgId = "grid-background-target";

function App() {
  const [word, setWord] = useState("");
  const [hzWriter, sethzWriter] = useState<HanziWriter>();

  const handleClick = useCallback(() => {
    if (hzWriter) {
      hzWriter.setCharacter(word).then(() => {
        hzWriter.loopCharacterAnimation();
      });
    } else {
      var writer = HanziWriter.create(svgId, word, {
        width: 200,
        height: 200,
        padding: 5,
        showOutline: true,
        delayBetweenLoops: 1500,
      });
      writer.loopCharacterAnimation();
      sethzWriter(writer);
    }
  }, [hzWriter, word]);

  const handleInputChange = useCallback((text: string) => {
    setWord(text);
  }, []);

  return (
    <div>
      <Space
        className="container"
        direction="vertical"
        size={"middle"}
        align={"center"}
      >
        <Row gutter={4} align={"middle"}>
          <Col span={16}>
            <Input
              maxLength={1}
              size={"large"}
              placeholder="输入单个汉字"
              onChange={(e) => handleInputChange(e.target.value)}
            ></Input>
          </Col>

          <Col span={8}>
            <Button
              size={"large"}
              type="primary"
              onClick={() => handleClick()}
              disabled={word === ""}
            >
              显示笔画
            </Button>
          </Col>
        </Row>
        <Grid />
      </Space>
    </div>
  );
}

export default App;
