import "../App.css";

const Grid = () => {
  return (
    <svg
      xmlns="http://www.w3.org/2000/svg"
      width="200"
      height="200"
      id="grid-background-target"
      className="svg"
    >
      <line
        x1="0"
        y1="0"
        x2="200"
        y2="200"
        stroke="#DDD"
        strokeDasharray="2,2"
      />
      <line
        x1="200"
        y1="0"
        x2="0"
        y2="200"
        stroke="#DDD"
        strokeDasharray="2,2"
      />
      <line
        x1="100"
        y1="0"
        x2="100"
        y2="200"
        stroke="#DDD"
        strokeDasharray="2,2"
      />
      <line
        x1="0"
        y1="100"
        x2="200"
        y2="100"
        stroke="#DDD"
        strokeDasharray="2,2"
      />
    </svg>
  );
};

export default Grid;
